export function getCookie(name) {
    const matches = document.cookie.match(
         new RegExp(
            "(?:^|; )" +
        name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, "\\$1") +
        "=([^;]*)"
    
         )
    );
    
  
    return matches ? decodeURIComponent(matches[1]) : undefined;
}



 export function parseCookie(name) {
  let authToken;
  authToken = name.split("Bearer ")[1];
  return authToken;
}

